
<template>
  <div class="sections">
    <h1>华金还款处理</h1>
    <div class="pl_body">
        <Row class="form_row">
            <Col :sm="24" :xs="24">
                <Form :model="formSel" class="downMenu">
                    <FormItem>
                        <Row>
                        <Col span="12">
                            <DatePicker :value="formSel.selectTm" format="yyyy-MM-dd" placeholder="还款时间" @on-change="dateChange"></DatePicker>
                            &nbsp;&nbsp;<span>应还华金总金额：￥<span v-text="countMn">0.00</span></span>&nbsp;&nbsp;
                          <Button type="primary" class="float_left" @click="loadContent(1)">查询</Button>
                        </Col>
                        <Col span="12">
                            <Button type="primary" class="float_right" @click="model1=!model1">立即偿还</Button>
                        </Col>
                        </Row>
                    </FormItem>
                </Form>
            </Col>
        </Row>
        <div class="table_show">
        <Table border :columns="columns1" :data="data1" ref="table"></Table></div>
        <Row :gutter="16" v-if="pageWin>=768">
            <Col :sm="5" :xs="24">
                <div class="total">共{{total}}页</div>
            </Col>
            <Col :sm="19" :xs="24" class="txt_right">
                <Page :total="total" show-elevator @on-change="changePage"></Page>
            </Col>
        </Row>
        <Page v-else :total="total" simple @on-change="changePage"></Page>
    </div>

    <Modal v-model="model1" title="立即偿还" @on-click="">
      <div slot="footer">
        <Button type="text" size="large" @click="model1=!model1">取消</Button>
        <Button type="primary" size="large" @click="payConfirm">确定</Button>
      </div>
        <Form :model="formSel" :label-width="60">
            <FormItem label="时间"><DatePicker :value="selectTm" format="yyyy-MM-dd" placement="bottom-start" placeholder="偿还时间" @on-change="dateChange1"></DatePicker></FormItem>
            <FormItem label="偿还流水号"><Input v-model="sn" placeholder="偿还流水号"></Input></FormItem>
            <FormItem label="偿还金额"><Input v-model="mn" placeholder="偿还金额"></Input></FormItem>
        </Form>
    </Modal>
  </div>
</template>

<script>
export default {
  name: 'sections',
  data () {
    return {
        tbWidth:100,
        total:100,
        model1:false,
        formSel:{
            selectTm:''
        },
        selectTm:'',
        countMn:'0.00',
        columns1: [
        {
            title: '客户姓名',
            key: 'hjUname'
        },
        {
            title: '手机号',
            key: 'hjMobl'
        },
        {
            title: '身份证号',
            key: 'hjIdCard'
        },
        {
            title: '华金产品ID',
            key: 'pdid'
        },
        {
            title: '放款总金额',
            key: 'amount'
        },
        {
            title: '当前期数',
            key: 'stage'
        },
        {
            title: '总期数',
            key: 'totalStage'
        },
        {
            title: '应还本金',
            key: 'planPrp'
        },
        {
            title: '应还利息',
            key: 'planInt'
        },
        {
            title: '应还总金额',
            key: 'planTotalAmt'
        },
        {
            title: '订单流水号',
            key: 'flowNo'
        },
        {
            title: '应还时间',
            key: 'planLtm',
            width:104
        },
        {
            title: '创建时间',
            key: 'crtTm',
            width:104
        },
        {
            title: '当前状态',
            key: 'status'
        }//,
//        {
//            title: '放款时间',
//            key: 'payTm',
//            width:104
//        }
        ],
        data1: []
        }
    },
    created:function(){
        if($(window).width()<768){
            var arr=this.columns1;
            for(var i=0;i<arr.length-1;i++){
                arr[i]['width']=this.tbWidth;
            }
            arr[arr.length-1]['width']=60;
            arr[arr.length-1]['fixed']='right';
            this.columns1=arr;
        }
    },
    computed :{
        pageWin(){
            return Math.max(window.innerWidth,document.documentElement.clientWidth,document.body.clientWidth);
        }
    },
    methods: {
        dateChange(date){
            this.formSel.selectTm=date;
        },
        dateChange1(date){
            this.selectTm=date;
        },
        changePage(page){
          this.loadContent(page);
        },
        chkDate(){

        },
        payConfirm(){

          var tm = this.selectTm
          var mn = this.mn
          var sn = this.sn

          if(tm == null || tm == ''){
            this.$Message.error('偿还时间不能为空')
            return
          }

          if(mn == null || mn == ''){
            this.$Message.error('偿还金额不能为空')
            return
          }

          if(sn == null || sn == ''){
            this.$Message.error('偿还流水号不能为空')
            return
          }

          /*
           queryTime : tm,
           repaySn :sn,
           amount :mn
           */

          let formData = new FormData();
          formData.append('queryTime', tm);
          formData.append('repaySn', sn);
          formData.append('amount', mn);

          this.axios
            .post('/hj/hjCompensatory', formData,
              {headers:{'Content-Type': 'multipart/form-data'}})
            .then(function(respon){
              console.log(JSON.stringify(respon.data))
              that.total = respon.data.count.count
              that.countMn = respon.data.count.repTotalAmt

              if(respon.data.resCode == 1){
                this.$Message.error('操作成功')
              }else{
                this.$Message.error('操作失败')
              }
            })
            .catch(function(resp){
              this.$Message.warning('系统异常');
            });
        },
        handleSubmit(name){
            /*this.$refs[name].validate((valid) => {
                if(valid){

                }else{
                    this.$Message.error('表单验证失败!');
                }
            })*/
        },
        countDesc(name){
            var desc=this.descForm.desc;
            this.descForm.descNm=desc.length;
            this.descForm.descCount=this.descForm.descNm+'/'+this.descRules.descLength;
            this.$refs[name].validate((valid) => {
                if(valid){

                }else{
                    this.$Message.error('表单验证失败!');
                }
            });
        },
        loadContent(page){
          var firstResult = (parseInt(page) - 1) * 10;
          var pageSize = 10;
          var that=this;

          let formData = new FormData();
          formData.append('queryTime', that.formSel.selectTm?that.formSel.selectTm:'');
          formData.append('pageStart', firstResult);
          formData.append('pageSize', pageSize);

          this.axios
            .post('/hj/queryHjRepayPlans', formData,
              {headers:{'Content-Type': 'multipart/form-data'}})
            .then(function(respon){
              console.log(JSON.stringify(respon.data))
              that.total = respon.data.count.count
              that.countMn = respon.data.count.repTotalAmt

              if(respon.data.resCode == 1){
                that.total = respon.data.count.count;
                respon.data.result.forEach(function(element) {
                  if (element.status == 0){
                    element.status = '待偿还'
                  }else if(element.status == 1){
                    element.status = '处理中'
                  }else if(element.status == 2){
                    element.status = '偿还成功'
                  }
                }, this);
                that.data1 = respon.data.result;
              }
            })
            .catch(function(resp){
              this.$Message.warning('系统异常');
            });
        }
    }
}
</script>




